.favorites {
  text-align: center;
  padding-bottom: 60px;
  padding-top: 60px;
  @media screen and (min-width: $tab) {
    padding-bottom: 100px;
    padding-top: 100px;
  }
  @media screen and (max-width: $tab) {
    .favorites__item:not(:nth-child(-n + 3)) {
      display: none;
    }
  }
}

.favorites__title {
  margin-bottom: 20px;
}

.favorites__intro {
  margin-bottom: 60px;

  @media screen and (min-width: $desk) {
    margin-bottom: 40px;
  }
}

.favorites__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  gap: 20px;
  justify-content: center;

  @media screen and (min-width: $tab) {
    gap: 32px;
  }
}

.favorites__item {
  width: 280px;
  height: 280px;
  border: 11px solid $main-color;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;

  @media screen and (min-width: $tab) {
    width: 336px;
    height: 336px;
    width: calc((100% - 32px) / 2);
    background: $primery-color;
    border: 14px solid $main-color;
  }

  @media screen and (min-width: $desk) {
    width: 395px;
    width: calc((100% - 64px) / 3);
    height: 395px;
  }
}

.favorites__item:hover,
.favorites__item:focus {
  border: 14px solid $accent-color;
}
